import { Component, Input, OnChanges } from "@angular/core";
import { DynamicTableService } from "../../../shared/services/dynamic-table.service";
import { TableOperationType, ActionType } from "../../../shared/models/types";

@Component({
  selector: "shw-table-operation",
  templateUrl: "./table-operation.component.html",
  styleUrls: ["./table-operation.component.scss"]
})
export class TableOperationComponent implements OnChanges {
  @Input() index: number;
  @Input() type: TableOperationType;

  deleteFlag = false;
  editFlag = false;
  infoFlag = false;

  constructor(private dynamicTableSrvc: DynamicTableService) {}

  ngOnInit(): void {
    // if (this.type == "delete") {
    //   // this.messageService.createBus("DELETE_ITEM");
    // }
  }

  ngOnChanges() {
    this.getOperationType();
  }

  onAction(action: ActionType) {
    if (action == "delete") {
      // this.messageService.sendData("DELETE_ITEM", {
      //   index: this.index,
      //   operation: action
      // });
    }
  }

  getOperationType() {
    switch (this.type) {
      case "delete":
        this.deleteFlag = true;
        break;
      case "edit":
        this.editFlag = true;
        break;
      case "info":
        this.infoFlag = true;
        break;
      case "edit-delete":
        this.deleteFlag = true;
        this.editFlag = true;
        break;
      case "edit-delete-info":
        this.deleteFlag = true;
        this.editFlag = true;
        this.infoFlag = true;
        break;
    }
  }
}
