import { Component, Input } from "@angular/core";
import { DynamicTableService } from "../../../shared/services/dynamic-table.service";

@Component({
  selector: "shw-row-selection",
  template: `
    <mat-checkbox (change)="onChange($event)"></mat-checkbox>
  `
})
export class RowSelectionComponent {
  @Input() index: number;

  constructor(private dynamicTableSrvc: DynamicTableService) {}

  onChange($event) {
    this.dynamicTableSrvc.emitSelectedRow({
      index: this.index,
      checked: $event.checked
    });
  }
}
