import { Input, Component, OnInit, SimpleChanges, Output, EventEmitter, ViewChild } from "@angular/core";
import { MatTableDataSource, MatPaginator } from "@angular/material";
import { TableCellConfig } from "../../shared/models/table-config";
import { TableOptions } from "../../shared/models/table-options";
import { DynamicTableService } from "../../shared/services/dynamic-table.service";

@Component({
  selector: "shw-dynamic-table",
  templateUrl: "./dynamic-table.component.html",
  styleUrls: ["./dynamic-table.component.scss"]
})
export class DynamicTableComponent implements OnInit {
  @Input() options: TableOptions;
  @Input() tableConfig: TableCellConfig[];
  @Input() data: any[];
  @Output() dataChanged = new EventEmitter();
  @Output() rowSelected = new EventEmitter();
  @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;
  tableData: MatTableDataSource<any>;
  tableOptions: TableOptions = {};

  constructor(private dynamicTableSrvc: DynamicTableService) {}

  ngOnInit() {
    this.allowEmptyHeader();
    this.dynamicTableSrvc.tableDataChanged = this.dataChanged;
    this.dynamicTableSrvc.tableRowSelected = this.rowSelected;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes["data"]) {
      this.tableData = new MatTableDataSource(this.data);
      setTimeout(() => {
        this.tableData.paginator = this.paginator;
      }, 0);
    }
    if (changes["options"]) {
      this.init();
      this.setOptions();
    }
  }

  init() {
    this.tableOptions = {
      inlineEdit: false,
      pagination: false,
      hasLoading: false
    };
  }

  setOptions() {
    if (this.options) {
      Object.keys(this.options).forEach(optionName => {
        if (this.tableOptions[optionName] != undefined) {
          this.tableOptions[optionName] = this.options[optionName];
        }
      });
    }
  }

  getModel(row, index: number) {
    let object = {};
    object[this.tableConfig[index].fieldName] = row[this.tableConfig[index].fieldName];
    return object;
  }

  // TODO
  getFields(row, index) {
    const dataType = this.tableConfig[index].dataType ? this.tableConfig[index].dataType : "input";
    let object = [
      {
        key: this.tableConfig[index].fieldName,
        type: dataType,
        templateOptions: {
          label: row[this.tableConfig[index].fieldName],
          required: true,
          options: [{ value: 1, name: "1" }, { value: 2, name: "2" }], // TODO
          valueProp: "value",
          labelProp: "name"
        }
      }
    ];
    return object;
  }

  getHeaders(): string[] {
    let headers = [];
    if (this.tableConfig) {
      this.tableConfig.forEach(element => {
        headers.push(element.headerTitle);
      });
    }
    return headers;
  }

  allowEmptyHeader() {
    if (this.tableConfig) {
      this.tableConfig.forEach(row => {
        row.headerTitle = row.headerTitle != "" ? row.headerTitle : " ";
      });
    }
  }
}
